import React from 'react';
import './experienceChart.css'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

export const ExperienceChart = ({experience})=>{ 
 const data = {
  labels: ['1-3 года', '3-6 лет', 'без опыта', "6 и более лет"],
  datasets: [
    {
      label: '# of Votes',
      data: [experience.between1And3, experience.between3And6, experience.noExperience, experience.moreThan6],
      backgroundColor: [
        'rgba(255, 99, 132, 0.2)',
        'rgba(54, 162, 235, 0.2)',
        'rgba(255, 206, 86, 0.2)',
        'rgba(75, 192, 192, 0.2)',
        'rgba(153, 102, 255, 0.2)',
        'rgba(255, 159, 64, 0.2)',
      ],
      borderColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(153, 102, 255, 1)',
        'rgba(255, 159, 64, 1)',
      ],
      borderWidth: 1,
    },
  ],
};

  return ( 
  <div className="experienceChart">
    <p>Опыт работы</p>
    <Pie data={data} />
  </div> )
}